@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/1.2/css/weather-icons.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#cities {
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 50%;
    margin-left:auto; 
    margin-right:auto;
  }
  
  #cities td, #cities th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  #cities tr:nth-child(even){background-color: #f2f2f2;}
  
  #cities tr:hover {background-color: #ddd;}
  
  #cities th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: rgb(17, 3, 68);
    color: white;
  }

  #cities td {
      color: black;
  }

  a, link{
    text-decoration:none
  }
input{
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid rgb(0, 0, 0);
    border-radius: 4px;
    box-sizing: border-box;
  }

  #formDiv {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
    border-collapse: collapse;
    width: 40%;
    margin-left:auto; 
    margin-right:auto;
  }

  #formDiv label{
      color: black;
  }

  .err{
    color: rgb(7, 0, 15);
  }
* {
	box-sizing: border-box;
	text-align: center;
  }
  
  body {
	font-family: Arial, Helvetica, sans-serif;
  }
  
.cityName{
	color: black;
}
  /* Float four columns side by side */
  .column {
	float: left;
	width: 15%;
	padding: 0 10px;
  }
  
  /* Remove extra left and right margins, due to padding */
  .row {
	  margin: 0 -5px;
	  float: inline-end; 
	  display:         flex;
	  flex-wrap:       wrap;
	  justify-content: center;
	}
  
  /* Clear floats after the columns */
  .row:after {
	content: "";
	display: table;
	clear: both;
  }
  
  /* Responsive columns */
  @media screen and (max-width: 600px) {
	.column {
	  width: 100%;
	  display: block;
	  margin-bottom: 20px;
	}
  }
  
  /* Style the counter cards */
  .card {
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
	padding: 16px;
	text-align: center;
	background-color: #5c5656;
  }

.temp_max{
	font-size: 20px;
	text-align: center;
	display: block;
	font-weight: 300;
	color: rgb(48, 2, 2);
	padding: 2px 0 0;
}

.temp_min{
	font-size: 20px;
	text-align: center;
	display: block;
	color: rgb(7, 3, 51);
	padding: 2px 0 0;
}

.date{
	color: rgb(255, 255, 255);
	text-align: center;
	text-transform: uppercase;
    font-size: 25px;
    padding: 5px;
	display: block;
}
.conditions{ 
	font-size: 15px;
	display: block;
	text-align: center;
	color: azure;
}
.wind {
	font-size: 15px;
}
.App-header {
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.topnav {
  overflow: hidden;
  background-color: #333;
}

.topnav .linkNav {
  float: left;
  color: #f8ecec;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

.topnav .linkNav:hover {
  background-color: #ddd;
  color: black;
}

.topnav .linkNav.active {
  background-color: #4CAF50;
  color: white;
}
